.side-menu-container{display:flex;flex-direction:column;font-size:13px;gap:50px;width:250px;height:100vh;padding:40px 15px 40px 15px;background-color:var(--green-700)}.side-menu-container .row1{text-align:center}.side-menu-container .row2 .menu-box ul{padding:0;margin:0}.side-menu-container .row2 .menu-box ul .menus{display:flex;flex-direction:column;position:relative}.side-menu-container .row2 .menu-box ul .menus .collapsible:after{content:"";font-family:FontAwesome;color:#fff;position:absolute;right:6px}.side-menu-container .row2 .menu-box ul .menus .active:after{content:"";font-family:FontAwesome;border:none}.side-menu-container .row2 .menu-box ul .menus .main-menu{display:flex;height:45px;align-items:center;padding-left:15px;letter-spacing:1px;gap:10px;color:var(--gray-300);border-left:5px solid var(--green-700);cursor:pointer}.side-menu-container .row2 .menu-box ul .menus .main-menu .dashboard-icon{display:flex;justify-content:center;width:20px;fill:var(--gray-300)}.side-menu-container .row2 .menu-box ul .menus .main-menu .pages-icon{display:flex;justify-content:center;width:20px;fill:var(--gray-300)}.side-menu-container .row2 .menu-box ul .menus .main-menu .categories-icon{display:flex;justify-content:center;width:20px;fill:var(--gray-300)}.side-menu-container .row2 .menu-box ul .menus .main-menu .customers-icon{display:flex;justify-content:center;width:20px;fill:var(--gray-300)}.side-menu-container .row2 .menu-box ul .menus .main-menu .banners-icon{display:flex;justify-content:center;width:20px;fill:var(--gray-300)}.side-menu-container .row2 .menu-box ul .menus .main-menu .gear-icon{display:flex;justify-content:center;width:20px;fill:var(--gray-300)}.side-menu-container .row2 .menu-box ul .menus .sub-menu{color:var(--gray-300);align-items:start;max-height:0;overflow:hidden;transition:max-height .3s ease-out}.side-menu-container .row2 .menu-box ul .menus .sub-menu li{display:flex;align-items:center;height:45px;padding-left:50px}.side-menu-container .row2 .menu-box ul .menus .active{color:#fff;border-left:5px solid var(--green-300);background:none}.side-menu-container .row2 .menu-box ul .menus .active .menu-icon{fill:var(--green-300)}.side-menu-container .row2 .menu-box ul .menus .main-menu:hover{color:#fff;border-left:5px solid var(--green-300);background:none}.side-menu-container .row2 .menu-box ul .menus .main-menu:hover .menu-icon{fill:var(--green-300)}.side-menu-container .row2 .menu-box ul .menus .sub-menu{letter-spacing:1px}.side-menu-container .row2 .menu-box ul .menus .sub-menu li:hover{color:#fff;cursor:pointer}.side-menu-container .row3 .logout-btn{display:flex;height:45px;align-items:center;padding-left:20px;gap:11px;letter-spacing:1px;color:var(--gray-300);border-top:1px solid var(--gray-300)}.side-menu-container .row3 .logout-btn .logout-icon{display:flex;justify-content:center;width:20px;fill:var(--gray-300)}.side-menu-container .row3 .logout-btn:hover{color:#fff;cursor:pointer}.side-menu-container .row3 .logout-btn:hover .logout-icon{fill:#fff}