.header-container{display:flex;align-items:center;justify-content:flex-end;width:100%;height:50px;background-color:#fff;padding:0 35px;box-shadow:0 0 5px 0 rgba(0,0,0,.08)}.header-container .header-nav{display:flex}.header-container .header-nav .avatar-icon:hover{cursor:pointer}.header-container .header-nav .avatar-icon:hover .my-profile-dropdown{display:block}.header-container .header-nav .avatar-icon{position:relative;padding:0 20px}.header-container .header-nav .avatar-icon img{width:27px;border-radius:100%}.header-container .header-nav .avatar-icon .my-profile-dropdown{display:none;position:absolute;min-width:230px;max-width:300px;top:42px;right:0px;color:#555;background:#fff;text-align:left;letter-spacing:1px;box-shadow:0 0 2px 2px rgba(0,0,0,.04);border-radius:3px;z-index:1}.header-container .header-nav .avatar-icon .my-profile-dropdown .triangle-icon{position:absolute;top:-23px;right:33px;fill:var(--gray-100);width:30px;z-index:2}.header-container .header-nav .avatar-icon .my-profile-dropdown .drop-down-avatar{padding:10px 12px}.header-container .header-nav .avatar-icon .my-profile-dropdown .drop-down-avatar img{width:50px}.header-container .header-nav .avatar-icon .my-profile-dropdown ul{padding:0 0 10px 0;margin:0;font-size:14px}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:first-child{display:flex;background-color:var(--gray-100);padding-right:12px}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:first-child .admin-name-role{display:flex;flex-direction:column;justify-content:center}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:first-child .admin-name-role .admin-name{font-weight:500;font-size:15px}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:nth-child(2){padding-top:5px}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:last-child{display:flex;align-items:center}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:last-child .logout-icon{width:16px;fill:var(--gray-400)}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li p{padding:5px 12px;margin:0}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:hover p{color:var(--green-400);cursor:pointer}.header-container .header-nav .avatar-icon .my-profile-dropdown ul li:hover .logout-icon{fill:var(--green-400);cursor:pointer}.header-container .header-nav .avatar-icon::after{content:"";font-family:FontAwesome;margin-left:6px;color:var(--gray-300)}.header-container .header-nav .bell-icon{width:20px;fill:var(--gray-300);margin:0 20px}.header-container .header-nav .gear-o-icon{width:20px;fill:var(--gray-300);margin-left:20px}.header-container .header-nav .vertical-line{border-left:1px solid var(--gray-300);height:27px}